.filter-checkbox {
  margin: 0 70px;
  display: flex;
  justify-content: center;
  padding-bottom: 70px;
  border-bottom: 1px solid #E8E8E8;
}

.filter-checkbox__checkbox {
  position: relative;
  width: 36px;
  height: 20px;
}

.filter-checkbox__switch {
  position: absolute;
  top: 0;
  left: 0;
  width: 36px;
  height: 20px;
  appearance: none;
  outline: none;
  border-radius: 25%/50%;
  box-shadow: 0 0 10px 10px #F9F9F9;
}

.filter-checkbox__switch::before {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #EBEBEB;
  transition: 0.5s;
}

.filter-checkbox__switch:checked::before {
  left: 18px;
  background-color: #2BE080;
}

.filter-checkbox__description {
  margin: 0 0 0 12px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}

@media screen and (max-width: 1024px) {
  .filter-checkbox {
    margin: 0 30px;
    padding-bottom: 50px;
  }
}

@media screen and (max-width: 630px) {
  .filter-checkbox {
    margin: 0 14px;
  }
}