.search-form {
  padding: 60px 70px 40px;
}

.search-form__form {
  position: relative;
}

.search-form__input {
  padding: 0 0 0 22px;
  border: none;
  background-color: #F9F9F9;
  border-radius: 40px;
  width: 100%;
  height: 46px;
}

.search-form__input-error {
  max-width: 396px;
  margin: 10px 0 0;
  padding-left: 22px;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  min-height: 12px;
  color: #FF4062;
  /* visibility: hidden; */
}

.search-form__button {
  position: absolute;
  top: 6px;
  right: 6px;
  background-image: url(../../images/search-form-button.svg);
  width: 34px;
  height: 34px;
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  opacity: 1;
  transition: opacity 0.5s;
}

.search-form__button:hover {
  opacity: 0.8;
}

@media screen and (max-width: 1024px) {
  .search-form {
    padding: 80px 30px 40px;
  }
}

@media screen and (max-width: 630px) {
  .search-form {
    padding: 80px 14px 40px;
  }
}