.portfolio {
  padding: 0 70px 125px;
}

.portfolio__header {
  margin: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 20px;
  color: #A0A0A0;
}

.portfolio__links {
  list-style: none;
  margin: 0;
  padding: 0;
}

.portfolio__link {
  margin: 20px 0 0;
  display: block;
  padding: 0 0 19px;
  text-decoration: none;
  border-bottom: 1px solid #E8E8E8;
  ;
  color: black;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 50px;
  position: relative;
}

.portfolio__link:first-of-type {
  margin: 50px 0 0;
}

.portfolio__link:hover {
  border: 1px solid black;
}

.portfolio__link-image {
  width: 29px;
  height: 60px;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (max-width: 1024px) {
  .portfolio {
    padding: 0 50px 90px;
  }

  .portfolio__link {
    font-size: 28px;
  }

  .portfolio__link-image {
    width: 27px;
    height: 60px;
  }
}

@media screen and (max-width: 630px) {
  .portfolio {
    padding: 0 14px 70px;
  }

  .portfolio__header {
    font-size: 14px;
    line-height: 20px;
  }

  .portfolio__link {
    font-size: 18px;
    line-height: 28px;
  }

  .portfolio__link:first-of-type {
    margin-top: 40px;
  }

  .portfolio__link-image {
    width: 18px;
    height: 28px;
  }
}